import { addError, removeError } from "./errorAction";
import { SET_CURRENT_USER } from "./movieConstants";
import api from "../../services/api";

export const setCurrentUser = user => ({
  type: SET_CURRENT_USER,
  user,
});

export const setToken = token => {
  api.setToken(token);
};

